<template>
  <el-container class="se">
    <Header title="" class="cm-header">
      <div class="se-input">
        <el-input :placeholder="$t('SearchPlaceHolder')" v-model="keyword">
          <el-button slot="append" @click="search(keyword)" icon="el-icon-search"></el-button>
        </el-input>
      </div>
    </Header>
    <aside class="se-keywords">
      <h4>{{$t('SearchKeywords')}}:</h4>
      <div class="se-tag-cont">
        <el-tag type="success" effect="dark" @click="search('Hospital')">{{$t('Hospital')}}</el-tag>
        <el-tag type="success" @click="search('Medical')">{{$t('Medical')}}</el-tag>
        <el-tag type="success" @click="search('Accident')">{{$t('Accident')}}</el-tag>
      </div>
    </aside>

    <el-main>
      <h4 class="se-results-label">{{$t("SearchResults")}}:</h4>
      <ul>
        <li @click="$linkTo(`/productDetail/${product.productCode}`)" class="se-product" v-for="(product, index) in products" :key="index">
          <el-card>
            <img class="se-icon" :src="productIcon" width="60px" height="60px" />
            <main class="se-product-main">
              <h4>{{product.productName}}</h4>
              <span class="se-desc">{{product.productDescription?product.productDescription.substr(0,50):''}}</span>
            </main>
          </el-card>
        </li>
      </ul>
    </el-main>
  </el-container>
</template>

<script>
import {postApi} from '../../common/utils'
export default {
  components: {
  },
  computed: {},
  methods: {
    search (keyword) {
      this.keyword = keyword
      const productListStr = sessionStorage.getItem('productList')
      if (productListStr) {
        let products = JSON.parse(productListStr)
        this.products = products.filter((item) => {
          return item.productName.toLowerCase().indexOf(keyword.toLowerCase()) >= 0
        })
      }
    }
  },
  data () {
    return {
      productIcon:'',
      keyword: '',
      products: []
    }
  },
  created () {
  },
  mounted () {

  }
}
</script>

<style>
  .se-input{
    flex: 1;
    width: auto;
    margin-left: var(--size-md);
  }
  .se .el-main{
    flex:1;
    overflow-y: scroll;
    padding:0;
  }
  .se-input .el-input__inner{
    height: 30px;
    line-height: 30px;
  }
  .se .hd-title{
    flex: 0;
    margin-right: 0;
  }
  .se-input  .el-button{
    padding:5px;
  }
  .se-icon{
    width: 60px;
    height: 60px;
    border: var(--border);
    padding:var(--size-xs);
    margin-right: var(--size-sm);
  }
  .se-product{
    margin-bottom: var(--size-sm);
  }
  .se .el-card__body{
    display: flex;
    padding: var(--size-sm);
  }
  .se-keywords{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: var(--size-sm);
    border-bottom: var(--border);
  }
  .se-product-main{
    text-align: left;
  }
  .se-tag-cont{
    padding: var(--size-sm) 0 0 0;
  }
  .se-results-label{
    text-align: left;
    padding:var(--size-sm);
  }
  .se-desc{
    color:var(--cor-green)
  }
</style>



